.switch {
  border: 1px solid #ccc;
  width: 50px;
  height: 26px;
  border-radius: 13px;
  cursor: pointer;
  display: inline-block; }
  .switchOn {
    background: #00d1b2; }
    .switchOn .switchToggle {
      left: 23px; }
  .switchDisabled {
    cursor: not-allowed; }

.switchToggle {
  border: 1px solid #999;
  box-shadow: 1px 1px 1px #ccc;
  width: 25px;
  height: 24px;
  left: 0;
  border-radius: 12px;
  background: white;
  position: relative;
  transition: left 0.2s ease-in-out; }



