@charset "UTF-8";
.shareName {
  color: #4a4a4a;
  line-height: 40px;
  font-size: 20px; }

.shareUrl {
  color: #9b9b9b;
  font-size: 12px;
  margin: 8px 0;
  width: 380px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.shareImg {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background: #86eea8;
  padding: 6px 0 0;
  display: inline-block;
  text-align: center; }

.scopes {
  margin-top: 15px;
  color: #4a4a4a;
  text-align: center; }
  .scopes li {
    display: inline;
    margin-right: 8px;
    font-size: 12px;
    font-style: italic; }
    .scopes li:before {
      content: '‧';
      margin-right: 2px; }

.card {
  position: relative;
  box-shadow: 0 1px 5px 1px rgba(0, 0, 0, 0.07);
  border-radius: 3px;
  perspective: 1000;
  padding: 15px;
  height: 305px;
  transition: 0.35s;
  transform-style: preserve-3d; }

.cardHover {
  transform: rotateY(180deg); }

.cardBack {
  height: 100%;
  transform: rotateY(180deg); }

.cardDelete {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.newCard {
  box-shadow: none;
  color: #4a4a4a;
  border: 2px dashed #c2f6d3;
  display: flex;
  align-items: center;
  justify-content: center; }

.newCardContent {
  cursor: pointer;
  text-align: center;
  color: #4a4a4a;
  font-size: 18px;
  font-style: italic;
  font-weight: 200;
  line-height: 29px; }

a.deleteIcon {
  color: red;
  opacity: 0.5; }
  a.deleteIcon:hover {
    opacity: 1; }

.form label {
  font-size: 10px;
  font-weight: bold;
  line-height: 10px;
  color: #000000; }

.form input {
  border: 1px solid #979797;
  box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.12);
  padding: 0 8px;
  border-radius: 2px; }
  .form input:focus {
    outline-color: #86eea8; }

.form .formName label {
  display: block;
  margin-bottom: 4px; }

.form .formName input {
  width: 100%;
  height: 30px; }

.form .formScopes input[type='checkbox'] {
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  height: 16px;
  width: 16px;
  padding: 2px;
  background: white;
  position: relative;
  border: 1px solid #e1e1e1;
  border-radius: 3px;
  box-shadow: none;
  vertical-align: middle; }
  .form .formScopes input[type='checkbox']:checked {
    border-color: #86eea8;
    background: #86eea8; }
    .form .formScopes input[type='checkbox']:checked::after {
      content: '\2714';
      font-size: 11px;
      color: white;
      position: absolute;
      top: 0px;
      left: 2.5px; }

.form .formScopes label {
  cursor: pointer;
  font-weight: normal;
  margin-left: 5px; }

button.buttonGreen {
  background-color: #86eea8;
  border-color: transparent;
  color: #fff; }
  button.buttonGreen:hover {
    background-color: #86eea8;
    border-color: transparent;
    color: #111; }



