.data .processor {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 15px;
  margin-bottom: 10px; }
  .data .processor .status {
    display: inline-block;
    position: relative;
    top: 3px;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    border-radius: 50%;
    background: #aaa; }
  .data .processor .isActive {
    background: #00d1b2; }
  .data .processor .isInactive {
    background: #d84544; }



