.labelWrapper {
  display: flex;
  align-items: center; }

.label {
  margin-left: 16px; }
  .label::-moz-selection {
    background: none; }
  .label::selection {
    background: none; }

.checkbox,
.checkbox[type='checkbox'] {
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  height: 16px;
  width: 16px;
  padding: 11px;
  background: white;
  position: relative;
  border: 2px solid #aaa;
  border-radius: 3px; }
  .checkbox:checked,
  .checkbox[type='checkbox']:checked {
    border-color: #00d1b2;
    background: #00d1b2; }
    .checkbox:checked::after,
    .checkbox[type='checkbox']:checked::after {
      content: '\2714';
      font-size: 14px;
      color: white;
      position: absolute;
      top: 2px;
      left: 5px; }



